.container {
  background-color: gainsboro;
  min-height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  justify-content: center;

  .wrapper {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 500px;
    align-items: flex-start;
    margin: 0 auto;
    border-radius: 3px;
    position: relative;
    background-color: lightgrey;
    border: solid #c6c6c6 1px;
    box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.25);

    .title {
      align-self: center;
      margin: 40px 0 40px;
      font-size: 26px;
      font-weight: bold;
    }

    .image {
      align-self: center;
      margin-bottom: 30px;
    }

    .message {
      align-self: center;
      color: grey;
      margin-top: 15px;
      font-size: 1.4rem;
      text-align: center;
      width: 300px;
    }

    .button {
      align-self: center;
      width: 300px;
      height: 35px;
      border-radius: 10px;
      font-weight: bolder;
      letter-spacing: 1.2px;
      font-size: 16px;
      margin-top: 50px;
    }
  }
}